<template>
  <div class="menu-control">
    <TopNav />
    <SideNav />
    <HeadingPage title="Xu hướng tìm kiếm" link="Trending" />
    <div class="content-page">
      <div class="row">
        <div class="col-md-6">
          <KeyTable
            :keys="google"
            logo="http://osip.ml/static/media/google.1ea89059.jpg"
            shopname="Google"
            address="www.google.com"
          />
        </div>
        <div class="col-md-6">
          <KeyTable
            :keys="shoppe"
            logo="http://osip.ml/static/media/shopee.52fb7992.png"
            shopname="Shoppe"
            address="www.shopper.vn"
          />
        </div>
        <div class="col-md-6">
          <KeyTable
            :keys="sendo"
            logo="data:image/png;base64,"
            shopname="Sendo"
            address="www.Sendo.vn"
          />
        </div>
        <div class="col-md-6">
          <KeyTable
            :keys="tiki"
            logo="http://osip.ml/static/media/tiki.48e52844.png"
            shopname="Tiki"
            address="www.tiki.vn"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TopNav from "../components/TopNav.vue";
import SideNav from "../components/SideNav.vue";
import HeadingPage from "../components/HeadingPage.vue";
import KeyTable from "../components/KeyTable.vue";
import UserService from "../services/UserService";
export default {
  name: "Trending",
  components: {
    TopNav,
    SideNav,
    HeadingPage,
    KeyTable
  },
  data() {
    return {
      google: [],
      sendo: [],
      tiki: [],
      shoppe: []
    };
  },
  created() {
    this.getHotWords();
  },
  methods: {
    async getHotWords() {
      const config = {
        headers: {
          Authorization: localStorage.getItem("access_token")
        }
      };
      await UserService.getHotWords(config).then(res => {
        this.google = res.data.hot_words.google;
        this.tiki = res.data.hot_words.tiki;
        this.shoppe = res.data.hot_words.shoppe;
        this.sendo = res.data.hot_words.sendo;
      });
    }
  }
};
</script>

